@import url(https://fonts.googleapis.com/css?family=Caladea&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  color: #c24141;
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid red;
  margin-bottom: 20px;
}

.setting-length {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.setting-length .break,
.setting-length .session {
  font-size: 1.4rem;
  margin: 0 20px;
  color: white;
}

.setting-length button {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 1px solid white;
  background-color: white;
  font-size: 1.2rem;
  text-align: center;
  font-weight: bold;
  margin: 0 5px;
}

.setting-length button:disabled {
  cursor: not-allowed;
}
/*# sourceMappingURL=pomodoro.css.map */
.timer-clock {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.timer-clock .time {
  height: 300px;
  width: 100%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url(/fcc_pomodoro_clock/static/media/tomato-svgrepo-com.ba199f9c.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.timer-clock .session-name {
  letter-spacing: 2px;
  color: #fff;
}

.timer-clock .time-left {
  display: flex;
  font-size: 3.5rem;
  color: red;
  letter-spacing: 2px;
  border: 2px solid gray;
  background-color: white;
  padding: 5px;
  border-radius: 5px;
  margin-top: 55px;
}

.timer-clock .controlers {
  margin-top: 20px;
}

.timer-clock .controlers button {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  margin: 0 5px;
  z-index: 99;
  color: black;
}

.timer-clock .play {
  background-image: url("https://img.icons8.com/ios/100/000000/circled-play.png");
}

.timer-clock .refresh {
  background-image: url(/fcc_pomodoro_clock/static/media/refresh-svgrepo-com.7e62db55.svg);
}
/*# sourceMappingURL=timer.css.map */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  padding: 15px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

body .app-container {
  width: 100vw;
  max-width: 500px;
  border: 1px solid gray;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  border-radius: 10px;
  padding: 10px;
  font-family: 'Caladea', Georgia, serif, sans-serif;
  background-color: #120a33;
}

@media (min-width: 450px) {
  body .app-container {
    width: 80vw;
  }
}

/*# sourceMappingURL=App.css.map */
